/* yatra-one-regular - latin */
@font-face {
  font-family: 'Yatra One';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/yatra-one-v5-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('../Yatra One Regular'), local('YatraOne-Regular'),
    url('../fonts/yatra-one-v5-latin-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/yatra-one-v5-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/yatra-one-v5-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/yatra-one-v5-latin-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/yatra-one-v5-latin-regular.svg#YatraOne') format('svg'); /* Legacy iOS */
}

/* pt-serif-regular - latin */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/pt-serif-v11-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('PT Serif'), local('PTSerif-Regular'),
    url('../fonts/pt-serif-v11-latin-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/pt-serif-v11-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/pt-serif-v11-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('../fonts/pt-serif-v11-latin-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/pt-serif-v11-latin-regular.svg#PTSerif') format('svg'); /* Legacy iOS */
}
